"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Progress } from "@/components/ui/progress";
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from "@/components/ui/tooltip";
import { useBacklog } from "@/contexts/BacklogContext";

interface SprintRoadmapProps {
  onQuiz: () => void;
}

export function SprintRoadmap({ onQuiz }: SprintRoadmapProps) {
  const { state } = useBacklog();
  const [selectedSprint, setSelectedSprint] = useState<string | null>(null);

  // Generate 6 sprints (12 weeks) plus trailing week
  const totalWeeks = 13;
  const sprintsPerTeam = 6;
  
  const generateRoadmapData = () => {
    const roadmap: Record<string, Record<number, any>> = {};
    
    state.teams.forEach((team) => {
      roadmap[team] = {};
      
      // Generate sprints for each team
      for (let sprintNum = 1; sprintNum <= sprintsPerTeam; sprintNum++) {
        const startWeek = (sprintNum - 1) * 2 + 1;
        const endWeek = startWeek + 1;
        
        // Find items assigned to this team
        const teamItems = state.items.filter(item => 
          item.assignedTeam === team && 
          (item.stage === 'build-execute' || item.stage === 'completed')
        );
        
        // Calculate capacity usage
        const totalStoryPoints = teamItems.reduce((sum, item) => sum + (item.storyPoints || 0), 0);
        const capacityUsed = Math.min(totalStoryPoints, state.sprintCapacity);
        const utilizationPercent = (capacityUsed / state.sprintCapacity) * 100;
        
        roadmap[team][sprintNum] = {
          id: `${team}-S${sprintNum}`,
          number: sprintNum,
          startWeek,
          endWeek,
          items: teamItems.slice(0, Math.floor(state.sprintCapacity / 5)), // Rough distribution
          capacity: state.sprintCapacity,
          used: capacityUsed,
          utilization: utilizationPercent,
          status: sprintNum <= 2 ? 'active' : sprintNum <= 4 ? 'planned' : 'future'
        };
      }
    });
    
    return roadmap;
  };

  const roadmapData = generateRoadmapData();

  const getSprintStatusColor = (status: string) => {
    switch (status) {
      case 'active': return 'bg-green-100 border-green-300 text-green-800';
      case 'planned': return 'bg-blue-100 border-blue-300 text-blue-800';
      case 'future': return 'bg-gray-100 border-gray-300 text-gray-600';
      default: return 'bg-gray-100 border-gray-300 text-gray-600';
    }
  };

  const getUtilizationColor = (utilization: number) => {
    if (utilization >= 90) return 'bg-red-500';
    if (utilization >= 70) return 'bg-yellow-500';
    return 'bg-green-500';
  };

  const handleExportSummary = () => {
    // Simple export functionality - in a real app, this would generate a PDF
    const summary = {
      teams: state.teams.length,
      totalItems: state.items.length,
      completedItems: state.items.filter(item => item.storyPoints).length,
      totalStoryPoints: state.items.reduce((sum, item) => sum + (item.storyPoints || 0), 0),
      sprints: sprintsPerTeam * state.teams.length
    };
    
    const summaryText = `
Sprint Roadmap Summary
=====================
Teams: ${summary.teams}
Total Items: ${summary.totalItems}
Estimated Items: ${summary.completedItems}
Total Story Points: ${summary.totalStoryPoints}
Total Sprints: ${summary.sprints}
Sprint Capacity: ${state.sprintCapacity} SP per sprint

Generated on: ${new Date().toLocaleDateString()}
    `.trim();
    
    const blob = new Blob([summaryText], { type: 'text/plain' });
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = 'sprint-roadmap-summary.txt';
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
  };

  return (
    <div className="space-y-6">
      <div className="text-center">
        <h1 className="text-3xl font-bold text-gray-900 mb-2">Sprint Roadmap</h1>
        <p className="text-gray-600 text-lg">
          Quarterly view of team sprints and capacity planning
        </p>
      </div>

      {/* Timeline Header */}
      <div className="bg-white p-6 rounded-lg shadow-sm border">
        <div className="flex items-center justify-between mb-4">
          <h2 className="text-xl font-semibold text-gray-800">Q1 Sprint Timeline</h2>
          <div className="flex items-center space-x-4">
            <Badge variant="outline">13 weeks</Badge>
            <Badge variant="outline">{sprintsPerTeam} sprints per team</Badge>
          </div>
        </div>
        
        <div className="grid grid-cols-13 gap-1 mb-4">
          {Array.from({ length: totalWeeks }, (_, i) => (
            <div key={i} className="text-center">
              <div className="text-xs font-medium text-gray-600 mb-1">W{i + 1}</div>
              <div className={`h-2 rounded ${
                i < 12 && i % 2 === 0 ? 'bg-blue-200' : 
                i < 12 && i % 2 === 1 ? 'bg-blue-300' : 
                'bg-gray-200'
              }`}></div>
            </div>
          ))}
        </div>
        
        <div className="flex items-center justify-center space-x-6 text-sm">
          <div className="flex items-center space-x-2">
            <div className="w-3 h-3 bg-blue-200 rounded"></div>
            <span>Sprint Week 1</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-3 h-3 bg-blue-300 rounded"></div>
            <span>Sprint Week 2</span>
          </div>
          <div className="flex items-center space-x-2">
            <div className="w-3 h-3 bg-gray-200 rounded"></div>
            <span>Buffer Week</span>
          </div>
        </div>
      </div>

      {/* Sprint Grid */}
      <div className="bg-white p-6 rounded-lg shadow-sm border">
        <h2 className="text-xl font-semibold text-gray-800 mb-4">Sprint Allocation Grid</h2>
        
        <div className="space-y-4">
          {state.teams.map((team) => (
            <div key={team} className="border rounded-lg p-4">
              <div className="flex items-center justify-between mb-3">
                <h3 className="font-semibold text-gray-900">{team}</h3>
                <Badge variant="outline">
                  Capacity: {state.sprintCapacity} SP/sprint
                </Badge>
              </div>
              
              <div className="grid grid-cols-6 gap-3">
                {Array.from({ length: sprintsPerTeam }, (_, i) => {
                  const sprintNum = i + 1;
                  const sprint = roadmapData[team][sprintNum];
                  
                  return (
                    <TooltipProvider key={sprintNum}>
                      <Tooltip>
                        <TooltipTrigger asChild>
                          <Card 
                            className={`cursor-pointer transition-all duration-200 hover:shadow-md ${
                              getSprintStatusColor(sprint.status)
                            } ${selectedSprint === sprint.id ? 'ring-2 ring-blue-500' : ''}`}
                            onClick={() => setSelectedSprint(
                              selectedSprint === sprint.id ? null : sprint.id
                            )}
                          >
                            <CardContent className="p-3">
                              <div className="text-center">
                                <div className="font-semibold text-sm mb-1">S{sprintNum}</div>
                                <div className="text-xs text-gray-600 mb-2">
                                  W{sprint.startWeek}-{sprint.endWeek}
                                </div>
                                <Progress 
                                  value={sprint.utilization} 
                                  className="h-2 mb-1"
                                />
                                <div className="text-xs font-medium">
                                  {sprint.used}/{sprint.capacity} SP
                                </div>
                              </div>
                            </CardContent>
                          </Card>
                        </TooltipTrigger>
                        <TooltipContent>
                          <div className="text-xs">
                            <p className="font-semibold">{team} - Sprint {sprintNum}</p>
                            <p>Weeks {sprint.startWeek}-{sprint.endWeek}</p>
                            <p>Capacity: {sprint.used}/{sprint.capacity} SP</p>
                            <p>Utilization: {sprint.utilization.toFixed(1)}%</p>
                            <p>Status: {sprint.status}</p>
                          </div>
                        </TooltipContent>
                      </Tooltip>
                    </TooltipProvider>
                  );
                })}
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Sprint Details */}
      {selectedSprint && (
        <div className="bg-white p-6 rounded-lg shadow-sm border">
          <h2 className="text-xl font-semibold text-gray-800 mb-4">Sprint Details</h2>
          
          {state.teams.map((team) => {
            const sprint = Object.values(roadmapData[team]).find(
              (s: any) => s.id === selectedSprint
            ) as any;
            
            if (!sprint) return null;
            
            return (
              <Card key={team} className="border-l-4 border-l-blue-500">
                <CardHeader>
                  <CardTitle className="flex items-center justify-between">
                    <span>{team} - Sprint {sprint.number}</span>
                    <Badge className={getSprintStatusColor(sprint.status)}>
                      {sprint.status}
                    </Badge>
                  </CardTitle>
                  <CardDescription>
                    Weeks {sprint.startWeek}-{sprint.endWeek} â€¢ {sprint.used}/{sprint.capacity} SP
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <div className="space-y-3">
                    <div className="flex items-center justify-between">
                      <span className="text-sm font-medium">Capacity Utilization:</span>
                      <div className="flex items-center space-x-2">
                        <Progress value={sprint.utilization} className="w-24 h-2" />
                        <span className="text-sm font-medium">
                          {sprint.utilization.toFixed(1)}%
                        </span>
                      </div>
                    </div>
                    
                    {sprint.items.length > 0 && (
                      <div>
                        <h4 className="font-medium text-gray-900 mb-2">Assigned Items:</h4>
                        <div className="space-y-2">
                          {sprint.items.map((item: any) => (
                            <div key={item.id} className="flex items-center justify-between p-2 bg-gray-50 rounded">
                              <span className="text-sm">{item.title}</span>
                              <Badge variant="outline">{item.storyPoints} SP</Badge>
                            </div>
                          ))}
                        </div>
                      </div>
                    )}
                    
                    {sprint.utilization > 100 && (
                      <div className="bg-red-50 p-3 rounded-lg border-l-4 border-red-500">
                        <p className="text-red-800 text-sm font-medium">
                          âš ï¸ Over capacity! Items will spill over to next sprint.
                        </p>
                      </div>
                    )}
                  </div>
                </CardContent>
              </Card>
            );
          })}
        </div>
      )}

      {/* Summary Stats */}
      <div className="grid gap-4 md:grid-cols-4">
        <Card>
          <CardContent className="p-4 text-center">
            <div className="text-2xl font-bold text-blue-600">
              {state.teams.length}
            </div>
            <div className="text-sm text-gray-600">Teams</div>
          </CardContent>
        </Card>
        
        <Card>
          <CardContent className="p-4 text-center">
            <div className="text-2xl font-bold text-green-600">
              {sprintsPerTeam * state.teams.length}
            </div>
            <div className="text-sm text-gray-600">Total Sprints</div>
          </CardContent>
        </Card>
        
        <Card>
          <CardContent className="p-4 text-center">
            <div className="text-2xl font-bold text-orange-600">
              {state.items.reduce((sum, item) => sum + (item.storyPoints || 0), 0)}
            </div>
            <div className="text-sm text-gray-600">Story Points</div>
          </CardContent>
        </Card>
        
        <Card>
          <CardContent className="p-4 text-center">
            <div className="text-2xl font-bold text-purple-600">
              {state.sprintCapacity}
            </div>
            <div className="text-sm text-gray-600">SP per Sprint</div>
          </CardContent>
        </Card>
      </div>

      {/* Action Buttons */}
      <div className="flex justify-between items-center">
        <Button
          onClick={handleExportSummary}
          variant="outline"
          className="border-gray-300 hover:bg-gray-50"
        >
          Download Flow Summary
        </Button>
        
        <Button
          onClick={onQuiz}
          className="bg-indigo-600 hover:bg-indigo-700 text-white px-6"
        >
          Take Quiz
        </Button>
      </div>

      <div className="bg-indigo-50 p-4 rounded-lg border-l-4 border-indigo-500">
        <h3 className="font-semibold text-indigo-900 mb-2">ðŸ“Š Roadmap Insights</h3>
        <p className="text-indigo-800 text-sm">
          This roadmap shows how backlog items are distributed across teams and sprints. 
          Notice how capacity constraints and spillover logic ensure realistic planning. 
          Click on individual sprints to see detailed allocation.
        </p>
      </div>
    </div>
  );
}
