"use client";

import { useState, useEffect } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Progress } from "@/components/ui/progress";
import { Textarea } from "@/components/ui/textarea";
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select";
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from "@/components/ui/tooltip";
import { useBacklog } from "@/contexts/BacklogContext";
import { toast } from "sonner";

interface DesignStageProps {
  onNext: () => void;
}

const DESIGN_STAGES = [
  { key: 'design-plan', label: 'Plan', description: 'Define requirements and scope' },
  { key: 'design-groom', label: 'Groom', description: 'Refine and clarify details' },
  { key: 'design-estimate', label: 'Estimate', description: 'Assign story points' }
] as const;

const STORY_POINT_OPTIONS = [1, 2, 3, 5, 8, 13];

export function DesignStage({ onNext }: DesignStageProps) {
  const { state, moveItemToStage, updateStoryPoints, updateDescription, updatePriority } = useBacklog();
  const [currentStageIndex, setCurrentStageIndex] = useState(0);
  const [processingItems, setProcessingItems] = useState<string[]>([]);
  const [editingItems, setEditingItems] = useState<Record<string, { description: string; priority: string }>>({});
  
  const designItems = state.items.filter(item => 
    item.stage.startsWith('design-') || item.stage === 'backlog'
  );

  const currentStage = DESIGN_STAGES[currentStageIndex];
  const itemsInCurrentStage = designItems.filter(item => item.stage === currentStage.key);
  const completedItems = state.items.filter(item => item.stage === 'design-estimate' && item.storyPoints);

  useEffect(() => {
    // Auto-advance items through plan stage only
    if (currentStage.key === 'design-plan') {
      const itemsToProcess = designItems.filter(item => item.stage === currentStage.key);
      
      if (itemsToProcess.length > 0 && processingItems.length === 0) {
        setProcessingItems(itemsToProcess.map(item => item.id));
        
        // Simulate processing time
        setTimeout(() => {
          const nextStageIndex = currentStageIndex + 1;
          const nextStage = DESIGN_STAGES[nextStageIndex];
          
          if (nextStage) {
            itemsToProcess.forEach(item => {
              moveItemToStage(item.id, nextStage.key);
              // Initialize editing state for grooming
              if (nextStage.key === 'design-groom') {
                setEditingItems(prev => ({
                  ...prev,
                  [item.id]: {
                    description: item.description,
                    priority: item.priority
                  }
                }));
              }
            });
            
            toast.success(`${itemsToProcess.length} item(s) moved to ${nextStage.label} stage`);
            setCurrentStageIndex(nextStageIndex);
            setProcessingItems([]);
          }
        }, 2000);
      }
    }
  }, [currentStageIndex, designItems, currentStage.key, processingItems.length, moveItemToStage]);

  const handleDescriptionChange = (itemId: string, description: string) => {
    setEditingItems(prev => ({
      ...prev,
      [itemId]: {
        ...prev[itemId],
        description
      }
    }));
  };

  const handlePriorityChange = (itemId: string, priority: string) => {
    setEditingItems(prev => ({
      ...prev,
      [itemId]: {
        ...prev[itemId],
        priority
      }
    }));
  };

  const handleSaveGrooming = (itemId: string) => {
    const editedItem = editingItems[itemId];
    if (editedItem) {
      updateDescription(itemId, editedItem.description);
      updatePriority(itemId, editedItem.priority as 'High' | 'Medium' | 'Low');
      toast.success("Item groomed successfully!");
    }
  };

  const handleProceedFromGrooming = () => {
    const groomingItems = itemsInCurrentStage;
    
    if (groomingItems.length === 0) {
      toast.error("No items to proceed with");
      return;
    }

    // Save all grooming changes
    groomingItems.forEach(item => {
      const editedItem = editingItems[item.id];
      if (editedItem) {
        updateDescription(item.id, editedItem.description);
        updatePriority(item.id, editedItem.priority as 'High' | 'Medium' | 'Low');
      }
    });

    // Move to estimate stage
    groomingItems.forEach(item => {
      moveItemToStage(item.id, 'design-estimate');
    });

    toast.success(`${groomingItems.length} item(s) moved to Estimate stage`);
    setCurrentStageIndex(2); // Move to estimate stage
  };

  const handleStoryPointSelection = (itemId: string, points: number) => {
    updateStoryPoints(itemId, points);
    toast.success(`Story points updated: ${points} SP (${points * 4} hours)`);
  };

  const handleProceedToBuild = () => {
    const estimatedItems = state.items.filter(item => 
      item.stage === 'design-estimate' && item.storyPoints
    );

    if (estimatedItems.length === 0) {
      toast.error("Please estimate at least one item before proceeding");
      return;
    }

    estimatedItems.forEach(item => {
      moveItemToStage(item.id, 'build-schedule');
    });

    toast.success(`${estimatedItems.length} item(s) sent to Build stage`);
    onNext();
  };

  const getProgressValue = () => {
    const totalStages = DESIGN_STAGES.length;
    return ((currentStageIndex + 1) / totalStages) * 100;
  };

  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case 'High': return 'bg-red-100 text-red-800 border-red-200';
      case 'Medium': return 'bg-yellow-100 text-yellow-800 border-yellow-200';
      case 'Low': return 'bg-green-100 text-green-800 border-green-200';
      default: return 'bg-gray-100 text-gray-800 border-gray-200';
    }
  };

  return (
    <div className="space-y-6">
      <div className="text-center">
        <h1 className="text-3xl font-bold text-gray-900 mb-2">Design Stage</h1>
        <p className="text-gray-600 text-lg">
          Plan, groom, and estimate backlog items
        </p>
      </div>

      {/* Progress Bar */}
      <div className="bg-white p-6 rounded-lg shadow-sm border">
        <div className="flex items-center justify-between mb-4">
          <h2 className="text-xl font-semibold text-gray-800">Design Progress</h2>
          <Badge variant="outline">
            Stage {currentStageIndex + 1} of {DESIGN_STAGES.length}
          </Badge>
        </div>
        
        <Progress value={getProgressValue()} className="mb-4" />
        
        <div className="flex justify-between text-sm">
          {DESIGN_STAGES.map((stage, index) => (
            <div 
              key={stage.key}
              className={`text-center ${
                index <= currentStageIndex ? 'text-blue-600 font-medium' : 'text-gray-400'
              }`}
            >
              <div className={`w-8 h-8 rounded-full mx-auto mb-1 flex items-center justify-center text-xs ${
                index < currentStageIndex 
                  ? 'bg-blue-600 text-white' 
                  : index === currentStageIndex
                  ? 'bg-blue-100 text-blue-600 border-2 border-blue-600'
                  : 'bg-gray-200 text-gray-400'
              }`}>
                {index < currentStageIndex ? 'âœ“' : index + 1}
              </div>
              <div className="font-medium">{stage.label}</div>
              <div className="text-xs text-gray-500">{stage.description}</div>
            </div>
          ))}
        </div>
      </div>

      {/* Current Stage Items */}
      <div className="bg-white p-6 rounded-lg shadow-sm border">
        <div className="flex items-center justify-between mb-4">
          <h2 className="text-xl font-semibold text-gray-800">
            {currentStage.label} Stage
          </h2>
          <Badge className="bg-blue-100 text-blue-800">
            {itemsInCurrentStage.length} items
          </Badge>
        </div>

        {currentStage.key === 'design-groom' ? (
          <div className="space-y-4">
            <p className="text-gray-600 mb-4">
              Refine item details, clarify requirements, and adjust priorities. Edit descriptions and priorities as needed.
            </p>
            
            {itemsInCurrentStage.map((item) => (
              <Card key={item.id} className="border-l-4 border-l-purple-500">
                <CardHeader className="pb-3">
                  <div className="flex items-start justify-between">
                    <CardTitle className="text-lg font-medium text-gray-900">
                      {item.title}
                    </CardTitle>
                    <div className="flex items-center space-x-2">
                      <span className="text-sm text-gray-600">Priority:</span>
                      <Select
                        value={editingItems[item.id]?.priority || item.priority}
                        onValueChange={(value) => handlePriorityChange(item.id, value)}
                      >
                        <SelectTrigger className="w-24">
                          <SelectValue />
                        </SelectTrigger>
                        <SelectContent>
                          <SelectItem value="High">High</SelectItem>
                          <SelectItem value="Medium">Medium</SelectItem>
                          <SelectItem value="Low">Low</SelectItem>
                        </SelectContent>
                      </Select>
                    </div>
                  </div>
                </CardHeader>
                
                <CardContent>
                  <div className="space-y-4">
                    <div>
                      <label className="text-sm font-medium text-gray-700 mb-2 block">
                        Description:
                      </label>
                      <Textarea
                        value={editingItems[item.id]?.description || item.description}
                        onChange={(e) => handleDescriptionChange(item.id, e.target.value)}
                        placeholder="Refine the item description..."
                        className="min-h-20"
                      />
                    </div>
                    
                    <div className="flex items-center justify-between">
                      <Badge className={getPriorityColor(editingItems[item.id]?.priority || item.priority)}>
                        {editingItems[item.id]?.priority || item.priority} Priority
                      </Badge>
                      <Button
                        onClick={() => handleSaveGrooming(item.id)}
                        size="sm"
                        className="bg-purple-600 hover:bg-purple-700"
                      >
                        Save Changes
                      </Button>
                    </div>
                  </div>
                </CardContent>
              </Card>
            ))}

            <div className="mt-6 text-center">
              <Button
                onClick={handleProceedFromGrooming}
                disabled={itemsInCurrentStage.length === 0}
                className="bg-purple-600 hover:bg-purple-700 text-white px-6"
              >
                Proceed to Estimation
              </Button>
            </div>
          </div>
        ) : currentStage.key === 'design-estimate' ? (
          <div className="space-y-4">
            <p className="text-gray-600 mb-4">
              Assign story points to each item. Remember: 1 Story Point = 4 hours of work.
            </p>
            
            {itemsInCurrentStage.map((item) => (
              <Card key={item.id} className="border-l-4 border-l-orange-500">
                <CardHeader className="pb-3">
                  <div className="flex items-start justify-between">
                    <div>
                      <CardTitle className="text-lg font-medium text-gray-900">
                        {item.title}
                      </CardTitle>
                      <CardDescription className="text-gray-600 mt-1">
                        {item.description}
                      </CardDescription>
                    </div>
                    <Badge className={getPriorityColor(item.priority)}>
                      {item.priority}
                    </Badge>
                  </div>
                </CardHeader>
                
                <CardContent>
                  <div className="flex items-center justify-between">
                    <span className="text-sm font-medium text-gray-700">Story Points:</span>
                    <div className="flex gap-2">
                      {STORY_POINT_OPTIONS.map((points) => (
                        <TooltipProvider key={points}>
                          <Tooltip>
                            <TooltipTrigger asChild>
                              <Button
                                variant={item.storyPoints === points ? "default" : "outline"}
                                size="sm"
                                onClick={() => handleStoryPointSelection(item.id, points)}
                                className={`w-12 h-8 text-xs ${
                                  item.storyPoints === points 
                                    ? 'bg-orange-600 hover:bg-orange-700' 
                                    : 'hover:bg-orange-50'
                                }`}
                              >
                                {points}
                              </Button>
                            </TooltipTrigger>
                            <TooltipContent>
                              <p>{points} SP = {points * 4} hours</p>
                            </TooltipContent>
                          </Tooltip>
                        </TooltipProvider>
                      ))}
                    </div>
                  </div>
                  
                  {item.storyPoints && (
                    <div className="mt-2 text-sm text-green-600 font-medium">
                      Estimated: {item.storyPoints} SP ({item.estimatedHours} hours)
                    </div>
                  )}
                </CardContent>
              </Card>
            ))}
          </div>
        ) : (
          <div className="space-y-4">
            <p className="text-gray-600 mb-4">
              Items are being processed in the {currentStage.label} stage...
            </p>
            
            {itemsInCurrentStage.map((item) => (
              <Card key={item.id} className="border-l-4 border-l-blue-500">
                <CardContent className="pt-4">
                  <div className="flex items-center justify-between">
                    <div>
                      <h3 className="font-medium text-gray-900">{item.title}</h3>
                      <p className="text-sm text-gray-600">{item.description}</p>
                    </div>
                    {processingItems.includes(item.id) && (
                      <div className="flex items-center space-x-2">
                        <div className="animate-spin rounded-full h-4 w-4 border-b-2 border-blue-600"></div>
                        <span className="text-sm text-blue-600">Processing...</span>
                      </div>
                    )}
                  </div>
                </CardContent>
              </Card>
            ))}
          </div>
        )}
      </div>

      {/* Completed Items Summary */}
      {completedItems.length > 0 && (
        <div className="bg-green-50 p-6 rounded-lg border-l-4 border-green-500">
          <h3 className="font-semibold text-green-900 mb-2">âœ… Completed Estimation</h3>
          <p className="text-green-800 text-sm mb-3">
            {completedItems.length} item(s) ready for Build stage
          </p>
          <div className="space-y-2">
            {completedItems.map((item) => (
              <div key={item.id} className="flex justify-between items-center text-sm">
                <span className="text-green-800">{item.title}</span>
                <Badge className="bg-green-100 text-green-800">
                  {item.storyPoints} SP
                </Badge>
              </div>
            ))}
          </div>
        </div>
      )}

      <div className="flex justify-between items-center">
        <div className="text-sm text-gray-600">
          <TooltipProvider>
            <Tooltip>
              <TooltipTrigger asChild>
                <span className="cursor-help underline decoration-dotted">
                  Story Point Reference
                </span>
              </TooltipTrigger>
              <TooltipContent>
                <div className="text-xs">
                  <p>1 SP = 4 hours</p>
                  <p>Fibonacci sequence: 1, 2, 3, 5, 8, 13</p>
                </div>
              </TooltipContent>
            </Tooltip>
          </TooltipProvider>
        </div>
        
        {currentStage.key === 'design-estimate' && (
          <Button
            onClick={handleProceedToBuild}
            disabled={completedItems.length === 0}
            className="bg-orange-600 hover:bg-orange-700 text-white px-6"
          >
            Proceed to Build
            {completedItems.length > 0 && (
              <Badge className="ml-2 bg-orange-500 text-white">
                {completedItems.length}
              </Badge>
            )}
          </Button>
        )}
      </div>

      {/* Enhanced Grooming Info */}
      {currentStage.key === 'design-groom' && (
        <div className="bg-purple-50 p-4 rounded-lg border-l-4 border-purple-500">
          <h3 className="font-semibold text-purple-900 mb-2">ðŸ” Grooming Best Practices</h3>
          <div className="text-purple-800 text-sm space-y-1">
            <p>â€¢ <strong>Clarify requirements:</strong> Add acceptance criteria and edge cases</p>
            <p>â€¢ <strong>Adjust priorities:</strong> Re-evaluate based on business value and dependencies</p>
            <p>â€¢ <strong>Break down large items:</strong> Ensure items are small enough for a sprint</p>
            <p>â€¢ <strong>Remove ambiguity:</strong> Make sure the team understands what needs to be built</p>
          </div>
        </div>
      )}
    </div>
  );
}
