"use client";

import { useState } from "react";
import { IntroScreen } from "@/components/IntroScreen";
import { BacklogRepository } from "@/components/BacklogRepository";
import { DesignStage } from "@/components/DesignStage";
import { BuildStage } from "@/components/BuildStage";
import { SprintRoadmap } from "@/components/SprintRoadmap";
import { LegendPanel } from "@/components/LegendPanel";
import { QuizMode } from "@/components/QuizMode";
import { BacklogProvider } from "@/contexts/BacklogContext";

export type AppStage = 'intro' | 'backlog' | 'design' | 'build' | 'roadmap' | 'quiz';

export default function Home() {
  const [currentStage, setCurrentStage] = useState<AppStage>('intro');

  const renderStage = () => {
    switch (currentStage) {
      case 'intro':
        return <IntroScreen onStart={() => setCurrentStage('backlog')} />;
      case 'backlog':
        return <BacklogRepository onNext={() => setCurrentStage('design')} />;
      case 'design':
        return <DesignStage onNext={() => setCurrentStage('build')} />;
      case 'build':
        return <BuildStage onNext={() => setCurrentStage('roadmap')} />;
      case 'roadmap':
        return <SprintRoadmap onQuiz={() => setCurrentStage('quiz')} />;
      case 'quiz':
        return <QuizMode onRestart={() => setCurrentStage('intro')} />;
      default:
        return <IntroScreen onStart={() => setCurrentStage('backlog')} />;
    }
  };

  return (
    <BacklogProvider>
      <div className="min-h-screen bg-gradient-to-br from-blue-50 to-teal-50">
        <div className="container mx-auto px-4 py-8">
          {renderStage()}
          <LegendPanel />
        </div>
      </div>
    </BacklogProvider>
  );
}
