"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { useBacklog } from "@/contexts/BacklogContext";
import { toast } from "sonner";

interface BacklogRepositoryProps {
  onNext: () => void;
}

export function BacklogRepository({ onNext }: BacklogRepositoryProps) {
  const { state, moveItemToStage } = useBacklog();
  const [selectedItems, setSelectedItems] = useState<string[]>([]);
  
  const backlogItems = state.items.filter(item => item.stage === 'backlog');

  const handleItemClick = (itemId: string) => {
    setSelectedItems(prev => 
      prev.includes(itemId) 
        ? prev.filter(id => id !== itemId)
        : [...prev, itemId]
    );
  };

  const handleSendToDesign = () => {
    if (selectedItems.length === 0) {
      toast.error("Please select at least one backlog item to proceed");
      return;
    }

    selectedItems.forEach(itemId => {
      moveItemToStage(itemId, 'design-plan');
    });

    toast.success(`${selectedItems.length} item(s) sent to Design stage`);
    setSelectedItems([]);
    onNext();
  };

  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case 'High': return 'bg-red-100 text-red-800 border-red-200';
      case 'Medium': return 'bg-yellow-100 text-yellow-800 border-yellow-200';
      case 'Low': return 'bg-green-100 text-green-800 border-green-200';
      default: return 'bg-gray-100 text-gray-800 border-gray-200';
    }
  };

  return (
    <div className="space-y-6">
      <div className="text-center">
        <h1 className="text-3xl font-bold text-gray-900 mb-2">Backlog Repository</h1>
        <p className="text-gray-600 text-lg">
          Select backlog items to move through the development process
        </p>
      </div>

      <div className="bg-white p-6 rounded-lg shadow-sm border">
        <div className="flex items-center justify-between mb-4">
          <h2 className="text-xl font-semibold text-gray-800">Available Items</h2>
          <Badge variant="outline" className="text-sm">
            {backlogItems.length} items in backlog
          </Badge>
        </div>

        <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-3">
          {backlogItems.map((item) => (
            <Card 
              key={item.id}
              className={`cursor-pointer transition-all duration-200 hover:shadow-md ${
                selectedItems.includes(item.id) 
                  ? 'ring-2 ring-blue-500 bg-blue-50' 
                  : 'hover:bg-gray-50'
              }`}
              onClick={() => handleItemClick(item.id)}
            >
              <CardHeader className="pb-3">
                <div className="flex items-start justify-between">
                  <CardTitle className="text-lg font-medium text-gray-900 leading-tight">
                    {item.title}
                  </CardTitle>
                  <Badge className={`ml-2 text-xs ${getPriorityColor(item.priority)}`}>
                    {item.priority}
                  </Badge>
                </div>
              </CardHeader>
              
              <CardContent>
                <CardDescription className="text-gray-600 text-sm leading-relaxed">
                  {item.description}
                </CardDescription>
                
                <div className="mt-3 flex items-center justify-between">
                  <span className="text-xs text-gray-500 font-medium">
                    ID: {item.id}
                  </span>
                  {selectedItems.includes(item.id) && (
                    <div className="w-5 h-5 bg-blue-500 rounded-full flex items-center justify-center">
                      <span className="text-white text-xs">âœ“</span>
                    </div>
                  )}
                </div>
              </CardContent>
            </Card>
          ))}
        </div>

        {backlogItems.length === 0 && (
          <div className="text-center py-12">
            <div className="text-gray-400 text-lg mb-2">No items in backlog</div>
            <p className="text-gray-500 text-sm">All items have been moved to the next stage</p>
          </div>
        )}
      </div>

      <div className="flex items-center justify-between">
        <div className="text-sm text-gray-600">
          {selectedItems.length > 0 && (
            <span className="font-medium text-blue-600">
              {selectedItems.length} item(s) selected
            </span>
          )}
        </div>
        
        <div className="space-x-3">
          <Button
            onClick={handleSendToDesign}
            disabled={selectedItems.length === 0}
            className="bg-blue-600 hover:bg-blue-700 text-white px-6"
          >
            Send to Design
            {selectedItems.length > 0 && (
              <Badge className="ml-2 bg-blue-500 text-white">
                {selectedItems.length}
              </Badge>
            )}
          </Button>
        </div>
      </div>

      <div className="bg-blue-50 p-4 rounded-lg border-l-4 border-blue-500">
        <h3 className="font-semibold text-blue-900 mb-2">ðŸ’¡ Tip</h3>
        <p className="text-blue-800 text-sm">
          Click on backlog items to select them. Selected items will move to the Design stage 
          where they'll go through planning, grooming, and estimation phases.
        </p>
      </div>
    </div>
  );
}
