"use client";

import { useState } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardDescription, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Progress } from "@/components/ui/progress";
import { RadioGroup, RadioGroupItem } from "@/components/ui/radio-group";
import { Label } from "@/components/ui/label";
import { useBacklog } from "@/contexts/BacklogContext";

interface QuizModeProps {
  onRestart: () => void;
}

interface QuizQuestion {
  id: string;
  question: string;
  options: string[];
  correctAnswer: number;
  explanation: string;
}

const quizQuestions: QuizQuestion[] = [
  {
    id: '1',
    question: 'What happens when a sprint reaches full capacity?',
    options: [
      'The sprint is extended by one week',
      'Items are automatically moved to the next available sprint',
      'The team works overtime to fit all items',
      'Items are removed from the backlog'
    ],
    correctAnswer: 1,
    explanation: 'When a sprint reaches capacity, items automatically spill over to the next available sprint to maintain realistic planning and prevent team burnout.'
  },
  {
    id: '2',
    question: 'How many story points can a team of 5 handle in a 2-week sprint?',
    options: [
      '50 story points',
      '80 story points',
      '100 story points',
      '120 story points'
    ],
    correctAnswer: 2,
    explanation: 'Calculation: 5 members Ã— 2 weeks Ã— 40 hours Ã· 4 hours per story point = 100 story points per sprint.'
  },
  {
    id: '3',
    question: 'Which stage involves story point estimation?',
    options: [
      'Plan stage',
      'Groom stage',
      'Estimate stage',
      'Execute stage'
    ],
    correctAnswer: 2,
    explanation: 'The Estimate stage is specifically where story points are assigned to backlog items, typically using Fibonacci numbers (1, 2, 3, 5, 8, 13).'
  },
  {
    id: '4',
    question: 'What does 1 story point represent in terms of time?',
    options: [
      '2 hours of work',
      '4 hours of work',
      '8 hours of work',
      '1 day of work'
    ],
    correctAnswer: 1,
    explanation: 'In this simulation, 1 story point equals 4 hours of work, including development, testing, and review activities.'
  },
  {
    id: '5',
    question: 'What is the primary purpose of backlog grooming?',
    options: [
      'To assign story points to items',
      'To move items between sprints',
      'To refine and clarify item details',
      'To assign items to team members'
    ],
    correctAnswer: 2,
    explanation: 'Backlog grooming involves refining items by adding details, clarifying requirements, and ensuring items are ready for estimation and development.'
  },
  {
    id: '6',
    question: 'How long does each sprint last in this simulation?',
    options: [
      '1 week',
      '2 weeks',
      '3 weeks',
      '4 weeks'
    ],
    correctAnswer: 1,
    explanation: 'Each sprint in this simulation lasts 2 weeks, which is a common duration in agile development teams.'
  }
];

export function QuizMode({ onRestart }: QuizModeProps) {
  const { resetSimulation } = useBacklog();
  const [currentQuestion, setCurrentQuestion] = useState(0);
  const [selectedAnswers, setSelectedAnswers] = useState<Record<string, number>>({});
  const [showResults, setShowResults] = useState(false);
  const [quizCompleted, setQuizCompleted] = useState(false);

  const handleAnswerSelect = (questionId: string, answerIndex: number) => {
    setSelectedAnswers(prev => ({
      ...prev,
      [questionId]: answerIndex
    }));
  };

  const handleNextQuestion = () => {
    if (currentQuestion < quizQuestions.length - 1) {
      setCurrentQuestion(prev => prev + 1);
    } else {
      setShowResults(true);
      setQuizCompleted(true);
    }
  };

  const handlePreviousQuestion = () => {
    if (currentQuestion > 0) {
      setCurrentQuestion(prev => prev - 1);
    }
  };

  const calculateScore = () => {
    let correct = 0;
    quizQuestions.forEach(question => {
      if (selectedAnswers[question.id] === question.correctAnswer) {
        correct++;
      }
    });
    return { correct, total: quizQuestions.length, percentage: (correct / quizQuestions.length) * 100 };
  };

  const handleRestartSimulation = () => {
    resetSimulation();
    onRestart();
  };

  const getScoreColor = (percentage: number) => {
    if (percentage >= 80) return 'text-green-600';
    if (percentage >= 60) return 'text-yellow-600';
    return 'text-red-600';
  };

  const getScoreBadgeColor = (percentage: number) => {
    if (percentage >= 80) return 'bg-green-100 text-green-800';
    if (percentage >= 60) return 'bg-yellow-100 text-yellow-800';
    return 'bg-red-100 text-red-800';
  };

  if (showResults) {
    const score = calculateScore();
    
    return (
      <div className="space-y-6">
        <div className="text-center">
          <h1 className="text-3xl font-bold text-gray-900 mb-2">Quiz Results</h1>
          <p className="text-gray-600 text-lg">
            How well did you understand the backlog flow?
          </p>
        </div>

        {/* Score Summary */}
        <Card className="max-w-2xl mx-auto">
          <CardHeader className="text-center">
            <CardTitle className="text-2xl">
              <span className={`font-bold ${getScoreColor(score.percentage)}`}>
                {score.correct}/{score.total}
              </span>
            </CardTitle>
            <CardDescription>
              <Badge className={`text-lg px-4 py-2 ${getScoreBadgeColor(score.percentage)}`}>
                {score.percentage.toFixed(0)}% Correct
              </Badge>
            </CardDescription>
          </CardHeader>
          <CardContent>
            <Progress value={score.percentage} className="mb-4" />
            <div className="text-center">
              {score.percentage >= 80 ? (
                <p className="text-green-700 font-medium">
                  ðŸŽ‰ Excellent! You have a strong understanding of backlog management and sprint planning.
                </p>
              ) : score.percentage >= 60 ? (
                <p className="text-yellow-700 font-medium">
                  ðŸ‘ Good job! You understand the basics. Review the areas you missed for improvement.
                </p>
              ) : (
                <p className="text-red-700 font-medium">
                  ðŸ“š Keep learning! Consider going through the simulation again to reinforce the concepts.
                </p>
              )}
            </div>
          </CardContent>
        </Card>

        {/* Detailed Results */}
        <div className="space-y-4">
          <h2 className="text-xl font-semibold text-gray-800 text-center">Review Your Answers</h2>
          
          {quizQuestions.map((question, index) => {
            const userAnswer = selectedAnswers[question.id];
            const isCorrect = userAnswer === question.correctAnswer;
            
            return (
              <Card key={question.id} className={`border-l-4 ${
                isCorrect ? 'border-l-green-500 bg-green-50' : 'border-l-red-500 bg-red-50'
              }`}>
                <CardHeader className="pb-3">
                  <div className="flex items-start justify-between">
                    <CardTitle className="text-lg font-medium text-gray-900">
                      Question {index + 1}
                    </CardTitle>
                    <Badge className={isCorrect ? 'bg-green-100 text-green-800' : 'bg-red-100 text-red-800'}>
                      {isCorrect ? 'âœ“ Correct' : 'âœ— Incorrect'}
                    </Badge>
                  </div>
                  <CardDescription className="text-gray-700 font-medium">
                    {question.question}
                  </CardDescription>
                </CardHeader>
                
                <CardContent>
                  <div className="space-y-3">
                    <div className="grid gap-2">
                      {question.options.map((option, optionIndex) => (
                        <div 
                          key={optionIndex}
                          className={`p-2 rounded border text-sm ${
                            optionIndex === question.correctAnswer 
                              ? 'bg-green-100 border-green-300 text-green-800 font-medium' 
                              : optionIndex === userAnswer && !isCorrect
                              ? 'bg-red-100 border-red-300 text-red-800'
                              : 'bg-gray-50 border-gray-200 text-gray-700'
                          }`}
                        >
                          <div className="flex items-center space-x-2">
                            {optionIndex === question.correctAnswer && (
                              <span className="text-green-600">âœ“</span>
                            )}
                            {optionIndex === userAnswer && !isCorrect && (
                              <span className="text-red-600">âœ—</span>
                            )}
                            <span>{option}</span>
                          </div>
                        </div>
                      ))}
                    </div>
                    
                    <div className="bg-blue-50 p-3 rounded-lg border border-blue-200">
                      <h4 className="font-medium text-blue-900 mb-1">Explanation:</h4>
                      <p className="text-blue-800 text-sm">{question.explanation}</p>
                    </div>
                  </div>
                </CardContent>
              </Card>
            );
          })}
        </div>

        {/* Action Buttons */}
        <div className="flex justify-center space-x-4">
          <Button
            onClick={handleRestartSimulation}
            className="bg-blue-600 hover:bg-blue-700 text-white px-6"
          >
            Try Simulation Again
          </Button>
          <Button
            onClick={() => window.location.reload()}
            variant="outline"
            className="px-6"
          >
            Reset Everything
          </Button>
        </div>
      </div>
    );
  }

  const question = quizQuestions[currentQuestion];
  const progress = ((currentQuestion + 1) / quizQuestions.length) * 100;

  return (
    <div className="space-y-6">
      <div className="text-center">
        <h1 className="text-3xl font-bold text-gray-900 mb-2">Knowledge Quiz</h1>
        <p className="text-gray-600 text-lg">
          Test your understanding of backlog management and sprint planning
        </p>
      </div>

      {/* Progress */}
      <div className="max-w-2xl mx-auto">
        <div className="flex items-center justify-between mb-2">
          <span className="text-sm font-medium text-gray-700">
            Question {currentQuestion + 1} of {quizQuestions.length}
          </span>
          <Badge variant="outline">
            {Math.round(progress)}% Complete
          </Badge>
        </div>
        <Progress value={progress} className="mb-4" />
      </div>

      {/* Question */}
      <Card className="max-w-2xl mx-auto">
        <CardHeader>
          <CardTitle className="text-xl font-semibold text-gray-900">
            {question.question}
          </CardTitle>
          <CardDescription>
            Select the best answer from the options below
          </CardDescription>
        </CardHeader>
        
        <CardContent>
          <RadioGroup
            value={selectedAnswers[question.id]?.toString() || ""}
            onValueChange={(value) => handleAnswerSelect(question.id, parseInt(value))}
          >
            {question.options.map((option, index) => (
              <div key={index} className="flex items-center space-x-2 p-3 rounded-lg hover:bg-gray-50 border border-transparent hover:border-gray-200">
                <RadioGroupItem value={index.toString()} id={`option-${index}`} />
                <Label htmlFor={`option-${index}`} className="flex-1 cursor-pointer text-sm">
                  {option}
                </Label>
              </div>
            ))}
          </RadioGroup>
        </CardContent>
      </Card>

      {/* Navigation */}
      <div className="flex justify-between items-center max-w-2xl mx-auto">
        <Button
          onClick={handlePreviousQuestion}
          disabled={currentQuestion === 0}
          variant="outline"
        >
          Previous
        </Button>
        
        <div className="text-sm text-gray-600">
          {selectedAnswers[question.id] !== undefined ? (
            <span className="text-green-600 font-medium">âœ“ Answered</span>
          ) : (
            <span>Select an answer to continue</span>
          )}
        </div>
        
        <Button
          onClick={handleNextQuestion}
          disabled={selectedAnswers[question.id] === undefined}
          className="bg-indigo-600 hover:bg-indigo-700 text-white"
        >
          {currentQuestion === quizQuestions.length - 1 ? 'Finish Quiz' : 'Next'}
        </Button>
      </div>

      <div className="bg-indigo-50 p-4 rounded-lg border-l-4 border-indigo-500 max-w-2xl mx-auto">
        <h3 className="font-semibold text-indigo-900 mb-2">ðŸ“ Quiz Tips</h3>
        <p className="text-indigo-800 text-sm">
          Think about what you learned during the simulation. The answers are based on the 
          concepts and processes you just experienced in the backlog flow.
        </p>
      </div>
    </div>
  );
}
