"use client";

import React, { createContext, useContext, useReducer, ReactNode } from 'react';

export interface BacklogItem {
  id: string;
  title: string;
  description: string;
  priority: 'High' | 'Medium' | 'Low';
  stage: 'backlog' | 'design-plan' | 'design-groom' | 'design-estimate' | 'build-schedule' | 'build-execute' | 'completed';
  storyPoints?: number;
  assignedTeam?: string;
  sprintNumber?: number;
  estimatedHours?: number;
}

export interface Sprint {
  id: string;
  number: number;
  team: string;
  capacity: number;
  usedCapacity: number;
  items: BacklogItem[];
  startWeek: number;
  endWeek: number;
}

interface BacklogState {
  items: BacklogItem[];
  sprints: Sprint[];
  teams: string[];
  currentWeek: number;
  sprintCapacity: number;
}

type BacklogAction =
  | { type: 'MOVE_ITEM_TO_STAGE'; itemId: string; stage: BacklogItem['stage'] }
  | { type: 'UPDATE_STORY_POINTS'; itemId: string; points: number }
  | { type: 'UPDATE_DESCRIPTION'; itemId: string; description: string }
  | { type: 'UPDATE_PRIORITY'; itemId: string; priority: BacklogItem['priority'] }
  | { type: 'ASSIGN_TO_TEAM'; itemId: string; team: string }
  | { type: 'ASSIGN_TO_SPRINT'; itemId: string; sprintId: string }
  | { type: 'CREATE_SPRINT'; team: string; startWeek: number }
  | { type: 'RESET_SIMULATION' };

const initialItems: BacklogItem[] = [
  {
    id: '1',
    title: 'User Authentication System',
    description: 'Implement login, registration, and password reset functionality',
    priority: 'High',
    stage: 'backlog'
  },
  {
    id: '2',
    title: 'Dashboard Analytics',
    description: 'Create interactive charts and metrics for user dashboard',
    priority: 'Medium',
    stage: 'backlog'
  },
  {
    id: '3',
    title: 'Mobile Responsive Design',
    description: 'Optimize application for mobile and tablet devices',
    priority: 'High',
    stage: 'backlog'
  },
  {
    id: '4',
    title: 'Email Notifications',
    description: 'Send automated emails for important user actions',
    priority: 'Low',
    stage: 'backlog'
  },
  {
    id: '5',
    title: 'Search Functionality',
    description: 'Add global search with filters and sorting options',
    priority: 'Medium',
    stage: 'backlog'
  },
  {
    id: '6',
    title: 'API Rate Limiting',
    description: 'Implement rate limiting to prevent API abuse',
    priority: 'High',
    stage: 'backlog'
  }
];

const initialState: BacklogState = {
  items: initialItems,
  sprints: [],
  teams: ['Team Alpha', 'Team Beta', 'Team Gamma'],
  currentWeek: 1,
  sprintCapacity: 100 // 5 members Ã— 2 weeks Ã— 40 hrs Ã· 4 hrs/SP = 100 SP
};

function backlogReducer(state: BacklogState, action: BacklogAction): BacklogState {
  switch (action.type) {
    case 'MOVE_ITEM_TO_STAGE':
      return {
        ...state,
        items: state.items.map(item =>
          item.id === action.itemId
            ? { ...item, stage: action.stage }
            : item
        )
      };

    case 'UPDATE_STORY_POINTS':
      return {
        ...state,
        items: state.items.map(item =>
          item.id === action.itemId
            ? { ...item, storyPoints: action.points, estimatedHours: action.points * 4 }
            : item
        )
      };

    case 'UPDATE_DESCRIPTION':
      return {
        ...state,
        items: state.items.map(item =>
          item.id === action.itemId
            ? { ...item, description: action.description }
            : item
        )
      };

    case 'UPDATE_PRIORITY':
      return {
        ...state,
        items: state.items.map(item =>
          item.id === action.itemId
            ? { ...item, priority: action.priority }
            : item
        )
      };

    case 'ASSIGN_TO_TEAM':
      return {
        ...state,
        items: state.items.map(item =>
          item.id === action.itemId
            ? { ...item, assignedTeam: action.team }
            : item
        )
      };

    case 'ASSIGN_TO_SPRINT':
      const sprint = state.sprints.find(s => s.id === action.sprintId);
      const item = state.items.find(i => i.id === action.itemId);
      
      if (!sprint || !item || !item.storyPoints) return state;

      const itemStoryPoints = item.storyPoints;

      // Check if sprint has capacity
      if (sprint.usedCapacity + itemStoryPoints > sprint.capacity) {
        // Find next available sprint or create new one
        const nextSprint = state.sprints.find(s => 
          s.team === sprint.team && 
          s.number > sprint.number &&
          s.usedCapacity + itemStoryPoints <= s.capacity
        );

        if (nextSprint) {
          return {
            ...state,
            items: state.items.map(i =>
              i.id === action.itemId
                ? { ...i, sprintNumber: nextSprint.number }
                : i
            ),
            sprints: state.sprints.map(s =>
              s.id === nextSprint.id
                ? { ...s, usedCapacity: s.usedCapacity + itemStoryPoints, items: [...s.items, item] }
                : s
            )
          };
        }
      }

      return {
        ...state,
        items: state.items.map(i =>
          i.id === action.itemId
            ? { ...i, sprintNumber: sprint.number }
            : i
        ),
        sprints: state.sprints.map(s =>
          s.id === action.sprintId
            ? { ...s, usedCapacity: s.usedCapacity + itemStoryPoints, items: [...s.items, item] }
            : s
        )
      };

    case 'CREATE_SPRINT':
      const newSprint: Sprint = {
        id: `sprint-${state.sprints.length + 1}`,
        number: Math.floor(state.sprints.length / state.teams.length) + 1,
        team: action.team,
        capacity: state.sprintCapacity,
        usedCapacity: 0,
        items: [],
        startWeek: action.startWeek,
        endWeek: action.startWeek + 1
      };

      return {
        ...state,
        sprints: [...state.sprints, newSprint]
      };

    case 'RESET_SIMULATION':
      return {
        ...initialState,
        items: initialItems.map(item => ({ ...item, stage: 'backlog' as const }))
      };

    default:
      return state;
  }
}

interface BacklogContextType {
  state: BacklogState;
  dispatch: React.Dispatch<BacklogAction>;
  moveItemToStage: (itemId: string, stage: BacklogItem['stage']) => void;
  updateStoryPoints: (itemId: string, points: number) => void;
  updateDescription: (itemId: string, description: string) => void;
  updatePriority: (itemId: string, priority: BacklogItem['priority']) => void;
  assignToTeam: (itemId: string, team: string) => void;
  assignToSprint: (itemId: string, sprintId: string) => void;
  createSprint: (team: string, startWeek: number) => void;
  resetSimulation: () => void;
}

const BacklogContext = createContext<BacklogContextType | undefined>(undefined);

export function BacklogProvider({ children }: { children: ReactNode }) {
  const [state, dispatch] = useReducer(backlogReducer, initialState);

  const moveItemToStage = (itemId: string, stage: BacklogItem['stage']) => {
    dispatch({ type: 'MOVE_ITEM_TO_STAGE', itemId, stage });
  };

  const updateStoryPoints = (itemId: string, points: number) => {
    dispatch({ type: 'UPDATE_STORY_POINTS', itemId, points });
  };

  const updateDescription = (itemId: string, description: string) => {
    dispatch({ type: 'UPDATE_DESCRIPTION', itemId, description });
  };

  const updatePriority = (itemId: string, priority: BacklogItem['priority']) => {
    dispatch({ type: 'UPDATE_PRIORITY', itemId, priority });
  };

  const assignToTeam = (itemId: string, team: string) => {
    dispatch({ type: 'ASSIGN_TO_TEAM', itemId, team });
  };

  const assignToSprint = (itemId: string, sprintId: string) => {
    dispatch({ type: 'ASSIGN_TO_SPRINT', itemId, sprintId });
  };

  const createSprint = (team: string, startWeek: number) => {
    dispatch({ type: 'CREATE_SPRINT', team, startWeek });
  };

  const resetSimulation = () => {
    dispatch({ type: 'RESET_SIMULATION' });
  };

  return (
    <BacklogContext.Provider value={{
      state,
      dispatch,
      moveItemToStage,
      updateStoryPoints,
      updateDescription,
      updatePriority,
      assignToTeam,
      assignToSprint,
      createSprint,
      resetSimulation
    }}>
      {children}
    </BacklogContext.Provider>
  );
}

export function useBacklog() {
  const context = useContext(BacklogContext);
  if (context === undefined) {
    throw new Error('useBacklog must be used within a BacklogProvider');
  }
  return context;
}
